body {
  background: linear-gradient(315deg, #65005e 3%, #3c84ce 38%, #30eee2 68%, #ff1919 98%) 0 0 / 400% 400% fixed;
  margin: 0;
  padding: 20px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  animation: gradient 15s infinite;
  overflow: auto;
}

@keyframes gradient {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 100% 100%;
  }

  100% {
    background-position: 0 0;
  }
}

.wave {
  width: 200%;
  height: 12em;
  opacity: .8;
  z-index: -1;
  background: rgba(255, 255, 255, .25);
  border-radius: 1000% 1000% 0 0;
  animation: wave 10s linear -3s infinite;
  position: fixed;
  bottom: 0;
  left: 0;
  transform: translate3d(0, 0, 0);
}

.wave:nth-of-type(2) {
  opacity: .8;
  animation: wave 18s linear infinite reverse;
  bottom: -1.25em;
}

.wave:nth-of-type(3) {
  opacity: .9;
  animation: wave 20s -1s infinite reverse;
  bottom: -2.5em;
}

@keyframes wave {
  2% {
    transform: translateX(1px);
  }

  25% {
    transform: translateX(-25%);
  }

  50% {
    transform: translateX(-50%);
  }

  75% {
    transform: translateX(-25%);
  }

  100% {
    transform: translateX(1px);
  }
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

.container {
  width: 1400px;
  margin: 110px auto 0;
}

.search-form {
  width: 100%;
  background: linear-gradient(315deg, rgba(101, 0, 94, .7) 3%, rgba(60, 132, 206, .7) 38%, rgba(48, 238, 226, .7) 68%, rgba(255, 25, 25, .7) 98%);
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 3px 5px rgba(0, 0, 0, .5);
}

.search-form__logo {
  margin-right: 50px;
}

.search-form__input {
  width: 300px;
  height: 30px;
  border: none;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  outline: none;
  padding: 5px 5px 5px 15px;
  font-size: 16px;
}

.search-form__input::-ms-input-placeholder {
  color: #ccc;
  font-size: 16px;
}

.search-form__input::placeholder {
  color: #ccc;
  font-size: 16px;
}

.search-form__button {
  width: 140px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

.search-form__button:hover, .search-form__button:focus {
  background-color: #ffff7e;
}

.img-search {
  margin-right: 10px;
}

.gallery {
  flex-wrap: wrap;
  margin: -10px;
  padding-top: 100px;
  display: flex;
}

.photo-card {
  width: calc(33.3333% - 20px);
  background-color: rgba(255, 255, 0, .5);
  border-radius: 8px;
  margin: 10px;
  overflow: hidden;
  box-shadow: 0 0 5px 2px rgba(0, 4, 56, .5);
}

.photo-card:hover, .photo-card:focus {
  box-shadow: 0 0 10px 5px rgba(255, 255, 255, .5);
}

.photo-img {
  height: 200px;
  width: 100%;
  object-fit: cover;
  overflow: hidden;
}

.info {
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

.info-item {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.end-collection-text {
  text-align: center;
  color: #000;
  margin: 30px 0;
  font-size: 24px;
  font-weight: 700;
}

.end-collection-text.is-hidden {
  display: none;
}

.load-more {
  width: 160px;
  height: 40px;
  cursor: pointer;
  color: #000;
  background-color: #dcdcdc;
  border: none;
  border-radius: 8px;
  margin-top: 50px;
  margin-bottom: 30px;
  margin-left: 50%;
  font-size: 16px;
  font-weight: 600;
  transform: translateX(-50%);
}

.load-more:hover, .load-more:focus {
  color: #000;
  background-color: #ffff7e;
}

.load-more.is-hidden {
  display: none;
}

